<script>
import { mapActions } from "vuex";
export default {
  data() {
    return {
      form: {
        madonvi: null,
        masothue: null,
        sodienthoai: null,
        nguoidaidien: null,
        nguoilienhe_new: null,
        sodienthoai_new: null,
        diachi_new: null,
        ngaygui: new Date().toISOString().substr(0, 10),
      },
    };
  },

  computed: {
    /* abc */
    currentUser() {
      return this.$store.state.auth.user;
    },
  },

  created() {
    this.form.madonvi = this.currentUser.MA_DONVI;
    this.form.masothue = this.currentUser.MS_THUE;
    this.form.sodienthoai = this.currentUser.SDT_NGUOI_DD;
    this.form.nguoidaidien = this.currentUser.NGUOI_DAI_DIEN;
  },

  methods: {
    ...mapActions(["addFeed"]),
    onSubmit() {
      /* if (
        this.form.nguoilienhe_new ||
        this.form.sodienthoai_new ||
        this.form.diachi_new
      ) {
        //this.addFeed(this.form)
        this.$store.dispatch('addFeed', this.form)
        alert(" gi");
      } else {
        alert("not ok");
      } */
      this.$swal
        .fire({
          text: "Bn chc chn mun gi yu cu n c quan BHXH khng?",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "ng ",
        })
        .then((result) => {
          if (result.isConfirmed) {
            if (
              this.form.nguoilienhe_new ||
              this.form.sodienthoai_new ||
              this.form.diachi_new
            ) {
              //this.addFeed(this.form)
              this.$store.dispatch("addFeed", this.form);
              this.$swal.fire(
                "Gi thnh cng yu cu !",
                "C quan BHXH s tip nhn, x l v phn hi li n v qua chuyn qun thu, xin cm n!"
              );
            } else {
              const Toast = this.$swal.mixin({
                toast: true,
                position: "top-end",
                showConfirmButton: false,
                timer: 3000,
                timerProgressBar: true,
                didOpen: (toast) => {
                  toast.addEventListener("mouseenter", this.$swal.stopTimer);
                  toast.addEventListener("mouseleave", this.$swal.resumeTimer);
                },
              });

              Toast.fire({
                icon: "warning",
                title: "Nhp t nht 1 thng tin cn thay i !",
              });
            }
          }
        });
    },
  },
};
</script>
